import React, { Component, createContext } from "react";
import { getAllEmails } from "../utils/api";

export const MailboxContext = createContext({
  inbox: []
});

export default class MailboxProvider extends Component {
  static Consumer = MailboxContext.Consumer;

  state = {
    inbox: []
  };

  componentDidMount() {
    getAllEmails().then(data => {
      console.log("data: ", data.send);
      const inbox = data.send;

      this.setState({
        inbox
      });
    });

    // this.setState({
    //   inbox: this.props.inbox
    //   // send: this.props.send
    // });
  }

  render() {
    console.log("this state inbox: ", this.state.inbox);
    return (
      <MailboxContext.Provider value={this.state.inbox}>
        {this.props.children}
      </MailboxContext.Provider>
    );
  }
}
